import React from 'react';

const e = React.createElement;

const RootLayout = ({ children }) => {
  return e('html', { lang: 'en' },
    e('head', null,
      e('title', null, 'Persistent Todo List'),
      e('meta', { name: 'description', content: 'A persistent todo list application' }),
      e('link', { rel: 'icon', href: '/favicon.ico' })
    ),
    e('body', null, children)
  );
};

export default RootLayout;

